import {ChangeDetectorRef, Component, ElementRef, NgZone, OnInit, Renderer2, ViewChild} from '@angular/core';
import {Web3Service} from '../../../services/web3.service';
import {ChainlistService} from '../../../services/chainlist.service';
import {Article} from '../../models/article';
import {IMyDateRangeModel, IMyDrpOptions} from 'mydaterangepicker';
import {Observable} from 'rxjs/Observable';
import {DateRange} from '../../models/date-range';


@Component({
  selector: 'app-chainlist',
  templateUrl: './chainlist.component.html',
  styleUrls: ['./chainlist.component.scss']
})
export class ChainlistComponent implements OnInit {

  accounts;
  account;
  balance;
  status = null;

  today = new Date();

  myDateRangePickerOptions: IMyDrpOptions = {
    // other options...
    dateFormat: 'dd.mm.yyyy',
    sunHighlight: true,
    markCurrentDay: true,
    markCurrentMonth: false,
    markCurrentYear: true,
    indicateInvalidDateRange: true,
    componentDisabled: false,
    // disableUntil: {
    //   year: this.today.getFullYear(),
    //   month: this.today.getMonth(),
    //   day: this.today.getDate() - 1
    // }
  };

  forsaleArticles: any = [];
  boughtArticles: any = [];
  sellBuyAction = false;
  dateRange: DateRange = null;
  model: any;


  @ViewChild('articlePrice')
  articlePrice: ElementRef;

  @ViewChild('articleDescription')
  articleDescription: ElementRef;

  @ViewChild('articleType')
  articleType: ElementRef;

  @ViewChild('accountTab')
  accountTab: ElementRef;
  @ViewChild('accountTabDiv')
  accountTabDiv: ElementRef;

  @ViewChild('forSaleTab')
  forSaleTab: ElementRef;
  @ViewChild('forSaleTabDiv')
  forSaleTabDiv: ElementRef;

  @ViewChild('transactionTab')
  transactionTab: ElementRef;
  @ViewChild('transactionTabDiv')
  transactionTabDiv: ElementRef;

  constructor(private _ngZone: NgZone,
              private web3Service: Web3Service,
              private cd: ChangeDetectorRef,
              private chainListService: ChainlistService,
              private elementRef: ElementRef,
              private renderer: Renderer2) {
  }

  ngOnInit() {
    this.onReady();
  }

  /**
   *  Get the initial account/owner balance so it can be displayed.
   */
  onReady = () => {
    this.web3Service.getAccounts()
      .subscribe(accs => {
        this.accounts = accs;
        this.account = this.accounts[0];

        this.refreshBalance();
        this.initContractEvents();

      }, err => {
        this.setStatus('Error; see log. ' + err)
      })
  };

  /**
   * get the balance of the current user/account
   */
  refreshBalance = () => {
    this.web3Service.getBalance(this.account)
      .subscribe(value => {
        this.balance = this.web3Service.convertFromWei(value, 'ether') + ' ETH';
        this.cd.detectChanges();
      });
    this.getBlockInfo()
  };

  /**
   * Get a certain nr of block or the latest
   * @param {number | string} blockNr
   * @returns {Observable<any>}
   */
  getBlockInfo = (blockNr: number | string = 'latest'): Observable<any> => {
    return this.web3Service.getBlock(blockNr);
  };

  /**
   * start listening to contract events
   */
  initContractEvents() {
    this.listenToEvents();
  };

  /**
   * date range picker change event handler
   * @param {IMyDateRangeModel} $event
   */
  onDateRangeChanged($event: IMyDateRangeModel) {
    this.dateRange = {
      beginDate: $event.beginJsDate,
      endDate: $event.endJsDate,
      formatted: $event.formatted
    };
  }

  /**
   *  listen to events triggered by smart contract
   *  when bandwidth is for sale
   *  when a bandwidth been sold
   */
  listenToEvents() {
    this.chainListService.getInstance()
      .subscribe((instance) => {
        // debugger;
        instance.LogSellArticle({}, {}).watch((error, event) => {
          console.log(event.args);
          this.updateUI();
          this.updateTabsForSale();
        });
      }, (error) => {
        this.setStatus('Error at getting instance: ' + error.message);
      });

    this.chainListService.getInstance()
      .subscribe((instance) => {
        instance.LogBuyArticle({}, {}).watch((error, event) => {
          this.updateUI();
          this.updateTabsBuys();
        });
      }, (error) => {
        this.setStatus('Error at getting instance: ' + error.message);
      });

    this.chainListService.getInstance()
      .subscribe((instance) => {
        instance.LogBalances({}, {}).watch((error, event) => {
          const etherprice = this.web3Service.convertFromWei(event.args.balanceBuyer, 'ether');
          console.log('Balance changed', etherprice, event.args);
        });
      }, (error) => {
        this.setStatus('Error at getting instance: ' + error.message);
      });

    this.chainListService.getInstance()
      .subscribe((instance) => {
        instance.LogPeriodCheck({}, {}).watch((error, event) => {

          console.log('Period changed', event.args);
        });
      }, (error) => {
        this.setStatus('Error at getting instance: ' + error.message);
      });
  }

  /**
   * update web ui parts reloading the products
   */
  updateUI() {
    this.reloadProducts();
    this.updateLoader(false);
    this.refreshBalance();
  }

  /**
   * reload the bandwidths and display them in a separate tabels (for sale table/ transactions table)
   */
  reloadProducts() {

    this.forsaleArticles = [];
    this.boughtArticles = [];
    this.chainListService.reloadArticles()
      .subscribe((articleIds) => {

        this.chainListService.getInstance()
          .subscribe((instance) => {

            const prods = [];

            if (articleIds.length === 0) {
              this.cd.detectChanges();
              return;
            }

            for (let i = 0; i < articleIds.length; i++) {
              const articleId = articleIds[i];

              instance.articles(articleId.toNumber())
                .then((article: any) => {
                  console.log(article);

                  const fr = Number(article[6]);
                  const to = Number(article[7]);
                  const now = new Date();
                  const dateFr = new Date(fr);
                  const dateTo = new Date(to);

                  const modArticle = {
                    id: articleId.toNumber(),
                    seller: article[1],
                    buyer: article[2] > 0x0 ? article[2] : null,
                    seller_short: article[1].substr(0, 10) + '...',
                    amount: article[3],
                    description: article[4],
                    price: this.web3Service.convertFromWei(article[5], 'ether') + ' ETH',
                    timestamp: '',
                    periodFrom: dateFr.getDate() + '.' + (dateFr.getMonth() + 1) + '.' + dateFr.getFullYear(),
                    periodTo: dateTo.getDate() + '.' + (dateTo.getMonth() + 1) + '.' + dateTo.getFullYear(),
                    hiddenPeriodTo: dateTo,
                    isInvalid: (now.getTime() > dateTo.getTime())
                  };

                  prods.push(modArticle);
                  if (modArticle.buyer) {
                    this.boughtArticles.push(modArticle);
                  }
                  if (i === articleIds.length - 1) {
                    this.forsaleArticles = prods.reverse();
                    this.boughtArticles = this.boughtArticles.reverse();
                    this.cd.detectChanges();
                  }

                });
            }
          });
      }, (error) => {
        this.setStatus('Error at reloading sold products: ' + error.message);
      })
  };

  /**
   * Sell an article by sending the form data to the smart contract
   */
  onSellArticleClick() {
    if (!this.account) {
      this.setStatus('No active account!');
      return;
    }
    this.updateLoader(true);

    const articleObject: Article = {
      amount: this.articleType.nativeElement.value,
      price: this.articlePrice.nativeElement.value,
      description: this.articleDescription.nativeElement.value,
      owner: {
        from: this.account,
        gas: 500000
      },
      periodFrom: this.dateRange.beginDate.getTime(),
      periodTo: this.dateRange.endDate.getTime()
    };

    // console.log(articleObject);

    this.chainListService.sellArticle(articleObject)
      .subscribe((resp) => {
        console.log('Sell article:', resp);
      }, (error) => {
        this.setStatus('Error at selling a product: ' + error.message);
      });
  };

  /**
   * Buy article from a seller by sending data to the smart contract
   * @param data
   */
  onBuyArticleClicked(data: any) {
    if (this.today.getTime() > data.hiddenPeriodTo.getTime()) {
      this.setStatus('Article bandwith is no more valid! Availability period expired!');
      // return;
    }
    if (!this.account) {
      this.setStatus('No active account!');
      return;
    }
    this.updateLoader(true);

    this.chainListService.buyArticle(this.account, data)
      .subscribe((resp) => {
        console.log('Buyed article:', resp);
      }, (error) => {
        this.setStatus('Error at buying a product: ' + error.message);
      })
  }

  /**
   * show loader that a block is mining
   * @param status
   */
  updateLoader(status) {
    this.sellBuyAction = status;
    this.cd.detectChanges();
  }

  /**
   * Show status messages for better understanding what happening behind the "scene"/ in the smart contract
   * @param message
   */
  setStatus = message => {
    this.sellBuyAction = false;
    this.status = message;
    this.cd.detectChanges();
  };


  /**
   * Update tabs
   */
  updateTabsBuys() {
    setTimeout(() => {
      this.renderer.removeClass(this.accountTab.nativeElement, 'active');
      this.renderer.removeClass(this.forSaleTab.nativeElement, 'active');
      this.renderer.addClass(this.transactionTab.nativeElement, 'active');

      this.renderer.removeClass(this.accountTabDiv.nativeElement, 'active');
      this.renderer.removeClass(this.forSaleTabDiv.nativeElement, 'active');
      this.renderer.addClass(this.transactionTabDiv.nativeElement, 'active');
      this.renderer.addClass(this.transactionTabDiv.nativeElement, 'in');

      this.cd.detectChanges();
    }, 100)
  }

  /**
   * update tabs
   */
  updateTabsForSale() {

    setTimeout(() => {
      this.renderer.removeClass(this.accountTab.nativeElement, 'active');
      this.renderer.removeClass(this.transactionTab.nativeElement, 'active');
      this.renderer.addClass(this.forSaleTab.nativeElement, 'active');

      this.renderer.removeClass(this.accountTabDiv.nativeElement, 'active');
      this.renderer.removeClass(this.transactionTabDiv.nativeElement, 'active');
      this.renderer.addClass(this.forSaleTabDiv.nativeElement, 'active');
      this.renderer.addClass(this.forSaleTabDiv.nativeElement, 'in');

      this.cd.detectChanges();
    }, 100)
  }

  /**
   * just for test purpose
   */
  getAccountBalances() {
    this.chainListService.getInstance()
      .subscribe((instance) => {
        instance.getAccountBalances().then(value => {
          console.log('Balances', this.web3Service.convertFromWei(value, 'ether'));
        });
      }, (error) => {
        this.setStatus('Error at getting account balances: ' + error.message);
      })
  }

}



