import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {Web3Service} from './web3.service'
import {Article} from '../app/models/article';

const chListArtifacts = require('../../build/contracts/ChainList.json');
const contract = require('truffle-contract');

@Injectable()
export class ChainlistService {

  ChannelList = contract(chListArtifacts);

  constructor(
    private web3Ser: Web3Service,
  ) {
    // Bootstrap the ChannelList abstraction for Use
    this.ChannelList.setProvider(web3Ser.web3.currentProvider);
  }

  reloadArticles(): Observable<any> {

    let meta;
    return Observable.create(observer => {
      this.ChannelList
        .deployed()
        .then(instance => {
          meta = instance;
          return meta.getArticlesProducts();
        })
        .then((data) => {
          observer.next(data);
          observer.complete();
        })
        .catch(e => {
          console.log(e);
          observer.error(e)
        });
    })
  }

  sellArticle(article: Article): Observable<any> {

    return Observable.create(observer => {
      this.ChannelList
        .deployed()
        .then(instance => {
          return instance.sellArticle(
            article.amount,
            article.description,
            this.web3Ser.convertToWei(article.price, 'ether'),
            article.periodFrom,
            article.periodTo,
            article.owner);
        })
        .then((data) => {
          observer.next(data);
          observer.complete();
        })
        .catch(e => {
          console.log(e);
          observer.error(e)
        });
    })
  }

  reverseString(data: string): string {
    return data.split('').reverse().join('');
  }

  buyArticle(account, article: any): Observable<any> {
    const priceEth = this.reverseString(article.price);
    const priceEthNumber =  this.reverseString(priceEth.substring(4, priceEth.length));

    return Observable.create(observer => {
      this.ChannelList
        .deployed()
        .then(instance => {
          return instance.buyArticle(article.id, {
            from: account,
            value: this.web3Ser.convertToWei(parseFloat(priceEthNumber), 'ether'),
            gas: 100000
          });
        })
        .then((data) => {
          observer.next(data);
          observer.complete();
        })
        .catch(e => {
          console.log(e);
          observer.error(e)
        });
    })
  }

  sendCoin(from, to, amount): Observable<any> {

    let meta;
    return Observable.create(observer => {
      this.ChannelList
        .deployed()
        .then(instance => {
          meta = instance;
          return meta.sendCoin(to, amount, {
            from: from
          });
        })
        .then((data) => {
          observer.next(data);
          observer.complete();
        })
        .catch(e => {
          console.log(e);
          observer.error(e)
        });
    })
  }

  getInstance(): Observable<any> {

    return Observable.create(observer => {
      this.ChannelList
        .deployed()
        .then(instance => instance)
        .then((data) => {
          observer.next(data);
          observer.complete();
        })
        .catch(e => {
          console.log(e);
          observer.error(e)
        });
    })
  }

}



